import { render, remove } from '../framework/render';
import { DEFAULT_POINT } from '../mock/const';
import PointFormView from '../view/main/point-form-view';
export default class NewPointPresenter {
    #mainListContainer;
    #handleDataChange;
    #handleDestroy;
    #pointNewComponent = null;
    #dataBase;
    #point;
    constructor({ mainListContainer, dataBase, onDataChange, onDestroy, }) {
        this.#mainListContainer = mainListContainer;
        this.#dataBase = dataBase;
        this.#point = DEFAULT_POINT;
        this.#handleDataChange = onDataChange;
        this.#handleDestroy = onDestroy;
    }
    init() {
        this.#point.id = crypto.randomUUID();
        if (this.#pointNewComponent !== null) {
            return;
        }
        this.#pointNewComponent = new PointFormView({
            point: this.#point,
            dataBase: this.#dataBase,
            isNewPoint: true,
            onFormSubmit: this.#handleFormSubmit,
            onDeleteClick: this.#handleDeleteClick,
            onFormClose: () => null,
        });
        render(this.#pointNewComponent, this.#mainListContainer, 'afterbegin');
        document.addEventListener('keydown', this.#escKeyDownHandler);
    }
    destroy() {
        if (this.#pointNewComponent === null) {
            return;
        }
        this.#handleDestroy();
        remove(this.#pointNewComponent);
        this.#pointNewComponent = null;
        document.removeEventListener('keydown', this.#escKeyDownHandler);
    }
    #handleFormSubmit = (newPoint) => {
        this.#handleDataChange(1 /* UserAction.ADD_POINT */, 1 /* UpdateType.MINOR */, { ...newPoint });
        this.destroy();
    };
    #handleDeleteClick = () => {
        this.destroy();
    };
    #escKeyDownHandler = (evt) => {
        if (evt.key === 'Escape') {
            evt.preventDefault();
            this.destroy();
        }
    };
}
